import { IStyleSheetConfig, InjectionMode, Stylesheet } from '@uifabric/merge-styles';
declare const DEV: boolean; // See webpack.config.js
const mergeStyles: IStyleSheetConfig = {
  injectionMode: DEV ? InjectionMode.appendChild : InjectionMode.insertNode,
  defaultPrefix: DEV ? 'where_is_waldo' : 'c'
};
Stylesheet.getInstance().setConfig(mergeStyles);
